<template>		
	<view>
		<view>
			<u-navbar :is-back="false" title=" " :background="bgColor" title-color="white" title-size="40" ></u-navbar>
		</view>
		
		<view class="top">
			
			<view class="top_info" @click="gotoLogin()">
				
				<view class="avatar">
					<u-avatar :src="avatarSrc" mode="square" size="large"></u-avatar>			
				</view>
				
				<view class="top_info_right">
					<view class="nickName">{{nickName}}</view>	
					
					<view class="top_info_right_son">
						<view class="day">与<span>TimeLaw</span>相遇的第<span>{{day}}</span>天</view>
									
						<view>
							<view class="loginMode">今天的不开心就到此为止吧</view>
						</view> 
						
					</view>
				</view>		
			</view>
		</view>
		
		<view class="edit_data" @click="goto_editData()">
			<img src="../../static/img/myself/edit_data.png">
			<view>编辑资料</view>
			<u-icon class="icon" name="arrow-right"></u-icon>
		</view>
		
		<view class="middle">	
			<img src="../../static/img/myself/photo.png">
			<view class="middle_right" @click="goto_photo()">
				<view>我的光阴</view>
				<u-icon class="icon" name="arrow-right"></u-icon>
			</view>
		</view>
		
		<view class="middle">
			<img src="../../static/img/myself/course.png">
			<view class="middle_right" @click="goto_course()">
				<view>我的教程</view>
				<u-icon class="icon" name="arrow-right"></u-icon>
			</view>
		</view>
		
		<view class="middle">
			<img src="../../static/img/myself/music.png">
			<view class="middle_right" @click="goto_music()">
				<view>我的音乐</view>
				<u-icon class="icon" name="arrow-right"></u-icon>
			</view>
		</view>
		
		<view class="middle">
			<img src="../../static/img/myself/wish.png">	
			<view class="middle_right_else" @click="goto_wish()">
				<view>我的愿望</view>
				<u-icon class="icon" name="arrow-right"></u-icon>
			</view>
		</view>
		

		<view class="edit_data" @click="goto_question()">
			<img src="../../static/img/myself/question.png">
			<view>意见反馈</view>
			<u-icon class="icon" name="arrow-right" ></u-icon>
		</view>
		
		<view v-show="isShow">
			<view class="edit_data" @click="logout()" >
				<img src="../../static/img/myself/out.png">
				<view>退出登录</view>
				<u-icon class="icon" name="arrow-right" ></u-icon>
			</view>
		</view>
		
	
		
		<u-tabbar v-model="current"  :list="menu" height="55px" :mid-button="true" bg-color="#bca5db" border-top="false" ></u-tabbar>	
	</view>		
</template>

<script>
	export default{
		data(){
			return{
				isShow:false,
				avatarSrc:'',
				nickName:'',
				day:'',
				bgColor:{
				  "backgroundImage": "linear-gradient(45deg, #1E90FF, 	#BA55D3)"
				},
				current:4,
				menu:[
					  {
						"pagePath": '/pages/index/index',
						"iconPath": "/static/img/tarbar/1_1.png",
						"selectedIconPath": "/static/img/tarbar/1_2.png", 
						"text": "首页",
						"count": 2,
						"isDot": true,
						"customIcon": false
					  },
					  {
						"pagePath": '/pages/plan/plan',
						"iconPath": "/static/img/tarbar/2_1.png",
						"selectedIconPath": "/static/img/tarbar/2_2.png",
						"text": "计划中心",
						"customIcon": false
					  },
					  {
						"pagePath": '/pages/time/time',
						"iconPath": "/static/img/tarbar/3_1.png",
						"selectedIconPath": "/static/img/tarbar/3_2.png",
						"text": "光阴",
						"midButton": true,
						"customIcon": false
					  },
					  {
						"pagePath": '/pages/life/life',
						"iconPath": "/static/img/tarbar/4_1.png",
						"selectedIconPath": "/static/img/tarbar/4_2.png",
						"text": "好友",
						"customIcon": false
					  },
					  {
						"pagePath": '/pages/mySelf/mySelf',
						"iconPath": "/static/img/tarbar/5_1.png",
						"selectedIconPath": "/static/img/tarbar/5_2.png",
						"text": "我",
						"count": 23,
						"isDot": false,
						"customIcon": false
					  }
				]
			}
		},
		onShow() {
			var token = uni.getStorageSync('token');
			if(token){
				this.isShow = true;
			uni.request({
				url:'http://localhost/users/getUserinfo',
				method:"GET",
				header:{
					token: uni.getStorageSync('token')
				},
				success: (e) => {
					console.log(e);
					if(e.data.data.nickName == null){
						this.nickName = '用户' + e.data.data.uid;
					}else{
						this.nickName = e.data.data.nickName;
					}
					
					this.avatarSrc = e.data.data.avatar;
					
				}
			});
		}
		
		},
		onLoad() {
			//校验用户是否登录
			var token = uni.getStorageSync('token');			
			if(token){
				this.isShow = true;
				uni.request({
					url:'http://localhost/users/getDayed',
					method:"GET",
					header:{
						"token": uni.getStorageSync('token')
					},
					success: (e) => {
						// console.log(e)
						this.day = e.data.data.days;
					}
				});
				
				uni.request({
					url:'http://localhost/users/getUserinfo',
					method:"GET",
					header:{
						token: uni.getStorageSync('token')
					},
					success: (e) => {
						console.log(e);
						if(e.data.data.nickName == null){
							this.nickName = '用户' + e.data.data.uid;
						}else{
							this.nickName = e.data.data.nickName;
						}
						
						this.avatarSrc = e.data.data.avatar;
						
					}
				});
			}else{
				this.isShow = false;
				this.nickName = "未登录";
				this.day = 1;
			}
			
		},
		methods:{
			gotoLogin(){
				var token = uni.getStorageSync('token');
				if(token){}
				else{
					uni.navigateTo({
						url:'../login/login'
					});
				}
			},
			logout(){
				uni.request({
					url:'http://localhost/users/logout',
					method:"GET",
					header:{
						token: uni.getStorageSync('token')
					},
					success: (e) => {
						console.log(e);
						uni.removeStorageSync('token');
						// uni.reLaunch({
						// 	url:'../login/login'
						// });
						this.isShow = false;
						this.nickName = "未登录";
						this.avatarSrc = null;
						this.day = 1;
					}
				});
			},
			goto_editData(){
				var token = uni.getStorageSync('token');
				if(token){
					uni.navigateTo({
						url:'../my_editData/my_editData'
					})
				}else{
					uni.navigateTo({
						url:'../login/login'
					})
				}
				
			},
			goto_photo(){
				var token = uni.getStorageSync('token');
				if(token){
					uni.navigateTo({
						url:'../my_photo/my_photo'
					})
				}else{
					uni.navigateTo({
						url:'../login/login'
					})
				}
			},
			goto_course(){
				var token = uni.getStorageSync('token');
				if(token){
					uni.navigateTo({
						url:'../my_course/my_course'
					})
				}else{
					uni.navigateTo({
						url:'../login/login'
					})
				}
			},
			goto_music(){
				var token = uni.getStorageSync('token');
				if(token){
					uni.navigateTo({
						url:'../my_music/my_music'
					})
				}else{
					uni.navigateTo({
						url:'../login/login'
					})
				}				
			},
			goto_wish(){
				var token = uni.getStorageSync('token');
				if(token){
					uni.navigateTo({
						url:'../my_wish/my_wish'
					})
				}else{
					uni.navigateTo({
						url:'../login/login'
					})
				}	
			},
			goto_question(){
				var token = uni.getStorageSync('token');
				if(token){
					uni.navigateTo({
						url:'../my_question/my_question'
					})
				}else{
					uni.navigateTo({
						url:'../login/login'
					})
				}	
			}
		}
	}
</script>

<style scoped>	
	.top{
		width: 750rpx;
		height: 280rpx;
		background-color: white;
		padding-left: 50rpx;
		padding-top: 80rpx;
	}
	.top_info{
		display: flex;
	}
	.top_info_right{
		margin-left: 50rpx;
	}
	.nickName{
		margin: 15rpx;
		font-size: 40rpx;
		margin-left: -5rpx;
		/* border: 1px solid #1E90FF; */
		font-weight: 600;
	}
	.top_info_right_son{
		margin-top: 20rpx;
	}
	.day span{
		color: #1E90FF;
	}
	.day{
		color: #8a8a8a;
	}
	.loginMode{
		color: #BA55D3;
		margin-top: 25rpx;
		margin-left: 0rpx;
	}
	.edit_data{
		display: flex;
		background-color: #fff;
		margin-top: 20rpx;
		height: 100rpx;
		margin-bottom: 20rpx;
	}
	.edit_data img{
		width: 40rpx;
		height: 40rpx;
		margin-top: 30rpx;
		margin-left: 35rpx;
	}
	.edit_data view{
		font-size: 30rpx;
		height: 100rpx;
		line-height: 100rpx;
		margin-left: 35rpx;
	}
	.edit_data .icon{
		margin-left: 450rpx;
		color: #bfbfbf;
	}
	.middle{
		display: flex;
		background-color: #fff;
		height: 100rpx;
	}
	.middle_right{
		display: flex;
		border-bottom: 1rpx solid #bfbfbf;
		margin-left: 35rpx;
		width: 640rpx;
	}
	.middle img{
		width: 40rpx;
		height: 40rpx;
		margin-top: 30rpx;
		margin-left: 35rpx;
	}
	.middle_right view{
		font-size: 30rpx;
		height: 100rpx;
		line-height: 100rpx;
	}
	.middle_right .icon{
		margin-left: 450rpx;
		color: #bfbfbf;
	}
	.middle_right_else{
		display: flex;
		margin-left: 35rpx;
		width: 640rpx;
	}
	.middle_right_else view{
		font-size: 30rpx;
		height: 100rpx;
		line-height: 100rpx;
	}
	.middle_right_else .icon{
		margin-left: 450rpx;
		color: #bfbfbf;
	}
	.iconout{
		margin-left: 505rpx;
		color: #bfbfbf;
	}
</style>